  --blue-primary: #3684E2;
  --blue-secondary: #0061D9;
  --blue-third: #0050B2;
  --yellow-primary: #FF9500;
  --yellow-secondary: #FFF5E5;
  --green-primary: #1BC400;
  --transition-duration-primary: 0.2s;
  --transition-duration-secondary: 0.5s;
  --transition-function-primary: ease-in-out;
